/** @description :    @2d  @3d [es6]
  * @author github.com/xaota
  * @types
  * * {integer} <-> {number} -  
  * * {natural} <-> {number} -    , .., {unsigned int} //    
  * * {percent} <-> {number} -    [0, 1]

  * @todo     . +     ,   . .
  * @feature  ,  `Vector.from(1,2,3).scale(2).reverse().normalize()`
  */

/** {Quatern}    @export @class
  * @field x y z w {number}  
  */
  export default class Quatern {
  /** {Quatern}   - @constructor
    * @param {number} x  
    * @param {number} y  
    * @param {number} z  
    * @param {number} w  
    */
    constructor(x, y, z, w) {
      Object.assign(this, {x, y, z, w});
    }

  /** @subsection @method */
  /**  
    * @return {Quatern} 
    */
    copy() {
      return new Quatern(this.x, this.y, this.z, this.w);
    }

  /**    
    * @return {Float32Array} [x, y, z, w]
    */
    data() {
      return new Float32Array([this.x, this.y, this.z, this.w]);
    }

  /**  
    * @return {number}  
    */
    norm() {
      return this.data().reduce((r, e) => r + e ** 2, 0);
    }

  /**  
    * @return {number}  
    */
    absolute() {
      return Math.hypot(this.x, this.y, this.z, this.w); // Math.sqrt(this.norm());
    }

  /**  
    * @return {Quatern} 
    */
    sign() {
      const abs = this.absolute();
      return abs === 0
        ? Quatern.empty
        : this.scale(1 / abs);
    }

  /**  
    * @return {number}  
    */
    argument() {
      // arg q = Math.acos(a / q.absolute()),  q = (a, vector)
      return Math.acos(this.w) * 2;
    }

  /**  
    * @return {Quatern} 
    */
    reverse() {
      return new Quatern(-this.x, -this.y, -this.z, this.w);
    }

  /**  ( ) 
    * @return {Quatern} 
    */
    inverse() {
      const norm = this.norm();
      return norm === 0
        ? Quatern.empty
        : this.reverse().scale(1 / norm);
    }

  /**    
    * @param {number} factor 
    * @return {Quatern} 
    */
    scale(factor) {
      return Quatern.data(this.data().map(e => e * factor));
    }

  /**  
    * @param {Quatern} quatern 
    * @return {Quatern} 
    */
    addition(quatern) {
      const A = this, B = quatern;
      return new Quatern(A.x + B.x, A.y + B.y, A.z + B.z, A.w + B.w);
    }

  /**   
    * @param {Quatern} quatern 
    * @return {Quatern} 
    */
    scalar(quatern) {
      const A = this, B = quatern;
      return new Quatern(A.x * B.x, A.y * B.y, A.z * B.z, A.w * B.w);
    }

  /**  
    * @param {Quatern} quatern 
    * @return {Quatern} 
    */
    multiply(quatern) {
      const A = this, B = quatern,
        x = A.w * B.x + A.x * B.w + A.y * B.z - A.z * B.y,
        y = A.w * B.y - A.x * B.z + A.y * B.w + A.z * B.x,
        z = A.w * B.z + A.x * B.y - A.y * B.x + A.z * B.w,
        w = A.w * B.w - A.x * B.x - A.y * B.y - A.z * B.z;
      return new Quatern(x, y, z, w);
    }

  /** @section @method @static */
  /**      
    * @param {number} angle   
    * @param {Vector} vector   
    * @return {Quatern} 
    */
    static from(angle, vector) {
      angle /= 2;
      const cos = Math.cos(angle), sin = Math.sin(angle),
        w = cos,
        x = vector.x * sin,
        y = vector.y * sin,
        z = vector.z * sin;
      return new Quatern(x, y, z, w);
    }

  /**    
    * @param {Float32Array} array   
    * @return {Quatern} 
    */
    static data(array) {
      return new Quatern(array[0], array[1], array[2], array[3]);
    }
  }

/** @section @const   */
  Quatern.empty = new Quatern(0, 0, 0, 0);

/** @section @private */
